import { Fade } from 'react-reveal';
const Header = () => {
    return (
        <div className="Header">
            <div className="overlay">
                <div className="textBox">
                    <Fade left><h3 className="title">    </h3></Fade>
                    <Fade right>
                        <p className="text">
                                  
                                       
                                     
                        </p>
                    </Fade>
                </div>
            </div>
        </div>
    );
};
export default Header;